import React, { useState, useEffect } from 'react';
import { Box, Text } from '@chakra-ui/react';

const HomePage = () => {
    const [typedText, setTypedText] = useState('');
    const introText = "Quick Learner";
    const outroText = "Programmer";

    useEffect(() => {
        let currentIndex = 0;

        const interval = setInterval(() => {
            setTypedText((prevText) => prevText + introText[currentIndex]);
            currentIndex++;

            if (currentIndex === introText.length) {
                clearInterval(interval);
                setTimeout(() => {
                    currentIndex = 0;
                    const outroInterval = setInterval(() => {
                        setTypedText((prevText) => prevText + outroText[currentIndex]);
                        currentIndex++;

                        if (currentIndex === outroText.length) {
                            clearInterval(outroInterval);
                        }
                    }, 100);
                }, 1000); // Pause for 1 second before typing "Programmer"
            }
        }, 100); // Adjust the timing as needed

        return () => clearInterval(interval); // Clear the interval if component unmounts
    }, []);

    return (
        <Box py={10} px={4} textAlign="center">
            <Text fontSize="xl" fontWeight="bold" mb={2}>
                Hey, I'm Deepak 
            </Text>
            <Text fontSize="lg" color="gray.600" mb={4}>
                {typedText}
            </Text>
            <Text fontSize="md" color="gray.700">
                Self-driven, quick starter, passionate programmer with a curious mind who enjoys solving problems and working on new projects.
            </Text>
        </Box>
    );
};

export default HomePage;
