import React from 'react';
import {
    Box,
    Link,
    Wrap,
    WrapItem,
    Menu,
    MenuButton,
    MenuList,
    MenuItem,
    useMediaQuery,
} from '@chakra-ui/react';


const Navbar = () => {

    const [isSmallerThan768] = useMediaQuery("(max-width: 768px)");
    const handleResumeDownload = () => {

        const downloadLink = document.createElement('a');
        downloadLink.href = 'https://drive.google.com/file/d/1DfOFeURD7CgoBOgMP3l2uLD19OYEqL3Z/view?usp=sharing';
        downloadLink.target = '_blank';
        downloadLink.download = 'resume.pdf';
        downloadLink.click();
        setTimeout(() => {
            window.location.href = 'https://drive.google.com/uc?export=download&id=1DfOFeURD7CgoBOgMP3l2uLD19OYEqL3Z';
        }, 2000); 
    };

    return (
        <Box as="nav" className="navbar" id="nav-menu">
            <Wrap spacing={0} justify={isSmallerThan768 ? "flex-start" : "space-evenly"} bg=" #1b6ec4;" py={2} px={4} position="fixed" top={0} width="100%" >
                {!isSmallerThan768 && (
                    <>
                        <WrapItem>
                            <Link href="#home" className="nav-link home" color="white" _hover={{ color: 'black' }} textDecoration="none">
                                Home
                            </Link>
                        </WrapItem>
                        <WrapItem>
                            <Link href="#about" className="nav-link about" color="white" _hover={{ color: 'black' }} textDecoration="none">
                                About
                            </Link>
                        </WrapItem>
                        <WrapItem>
                            <Link href="#skills" className="nav-link skills" color="white" _hover={{ color: 'black' }} textDecoration="none">
                                Skills
                            </Link>
                        </WrapItem>
                        <WrapItem>
                            <Link href="#projects" className="nav-link projects" color="white" _hover={{ color: 'black' }} textDecoration="none">
                                Projects
                            </Link>
                        </WrapItem>
                        <WrapItem>
                            <Link href="#contact" className="nav-link contact" color="white" _hover={{ color: 'black' }} textDecoration="none">
                                Contact
                            </Link>
                        </WrapItem>
                    </>
                )}
                <WrapItem>
                    {isSmallerThan768 ? (
                        <Menu>
                            <MenuButton as={Link} className="nav-link resume" color="white" textDecoration="none" _hover={{ color: 'white' }}>
                                Menu
                            </MenuButton>
                            <MenuList bg="#111111" color="white" placement="bottom-start">
                                <MenuItem>
                                    <Link
                                        href="#home"
                                        className="nav-link home"
                                        color="black"
                                        textDecoration="none"
                                        style={{
                                            fontSize: '18px',
                                            padding: '10px 20px',
                                            borderRadius: '5px',
                                        }}
                                    >
                                        Home
                                    </Link>
                                </MenuItem>
                                <MenuItem>
                                    <Link
                                        href="#about"
                                        className="nav-link about"
                                        color="black"
                                        textDecoration="none"
                                    >
                                        About
                                    </Link>
                                </MenuItem>
                                <MenuItem>
                                    <Link
                                        href="#skills"
                                        className="nav-link skills"
                                        color="black"
                                        textDecoration="none"
                                    >
                                        Skills
                                    </Link>
                                </MenuItem>
                                <MenuItem>
                                    <Link
                                        href="#projects"
                                        className="nav-link projects"
                                        color="black"
                                        textDecoration="none"
                                    >
                                        Projects
                                    </Link>
                                </MenuItem>
                                <MenuItem>
                                    <Link
                                        href="#contact"
                                        className="nav-link contact"
                                        color="black"
                                        textDecoration="none"
                                    >
                                        Contact
                                    </Link>
                                </MenuItem>
                                <MenuItem>
                                <Link
                            onClick={handleResumeDownload}
                            className="nav-link resume"
                            color="black"
                            textDecoration="none"
                        >
                            Go to Resume
                        </Link>

                                </MenuItem>
                            </MenuList>
                        </Menu>
                    ) : (
                        <Link
                        onClick={handleResumeDownload}
                        className="nav-link resume"
                        color="white"
                        _hover={{ color: 'black' }} 
                        textDecoration="none"
                    >
                        Resume
                    </Link>


                    )}
                </WrapItem>
            </Wrap>
        </Box>
    );
};

export default Navbar;
