import React from 'react';
import { Box, Heading, Flex, Image } from '@chakra-ui/react';

const TechStack = () => {
    const techSkills = [
        {
            name: 'JavaScript',
            imageSrc: 'https://upload.wikimedia.org/wikipedia/commons/thumb/9/99/Unofficial_JavaScript_logo_2.svg/512px-Unofficial_JavaScript_logo_2.svg.png',
        },
        {
            name: 'HTML',
            imageSrc: 'https://upload.wikimedia.org/wikipedia/commons/thumb/6/61/HTML5_logo_and_wordmark.svg/512px-HTML5_logo_and_wordmark.svg.png',
        },
        {
            name: 'CSS',
            imageSrc: 'https://upload.wikimedia.org/wikipedia/commons/thumb/3/3d/CSS.3.svg/512px-CSS.3.svg.png',
        },
        {
            name: 'MongoDB',
            imageSrc: 'https://upload.wikimedia.org/wikipedia/commons/thumb/9/93/MongoDB_Logo.svg/512px-MongoDB_Logo.svg.png',
        },
        {
            name: 'Express',
            imageSrc: 'https://upload.wikimedia.org/wikipedia/commons/thumb/6/64/Expressjs.png/512px-Expressjs.png',
        },
        {
            name: 'Chakra UI',
            imageSrc: 'https://avatars.githubusercontent.com/u/54212428?s=200&v=4',
        },
        {
            name: 'Redux',
            imageSrc: 'https://raw.githubusercontent.com/reduxjs/redux/master/logo/logo.png',
        },
        {
            name: 'Node.js',
            imageSrc: 'https://nodejs.org/static/images/logo-light.svg',
        },
        {
            name: 'Postman',
            imageSrc: 'https://assets.getpostman.com/common-share/postman-logo-horizontal-white.svg',
        },
        // Add more skills here
    ];

    return (
        <Box as="section" id="skills" p={8} marginBottom="100px" >
        <Heading as="h2" size="xl" mb={4} textAlign="center" paddingBottom="20px" marginTop={"50px"}>
            Tech Stack
        </Heading>
        <Box display="grid" width={{base:"100%",md:"70%"}} margin="auto" gridTemplateColumns="repeat(auto-fill, minmax(200px, 1fr))" gap={4}>
            {techSkills.map((skill) => (
                <Box
                    key={skill.name}
                    p={4}
                    textAlign="center"
                    borderWidth={1}
                    borderRadius="md"
                    bg="white"
                    transition="transform 0.2s"
                    _hover={{ transform: 'scale(1.05)' }}
                >
                    <Image
                        src={skill.imageSrc}
                        alt={skill.name}
                        maxW="80px"
                        mx="auto"
                    />
                    <Heading as="h2" size="md" mt={2}>
                        {skill.name}
                    </Heading>
                </Box>
            ))}
        </Box>
    </Box>
    );
};

export default TechStack;
