import React from 'react';
import { Box, Image, Heading, Text } from '@chakra-ui/react';
import personalPhoto from "./photos/personalPhoto.jpg";

function About() {
    return (
        <Box className="about section" id="about" p={{ base: '10px', md: '20px' }} display="flex" flexDirection="column" alignItems="center" width="100%" marginTop={"50px"} >

            <Box className="profile-image" marginBottom="20px" margin={"auto"} alignItems={"center"}>
                <Image src={personalPhoto} alt="" width={"100%"} height={"200px"} borderRadius={"50%"}  />
            </Box>

        <Box textAlign="center" width={{ base: '90%', md: '60%' }} margin={"auto"} >
        <Heading as="h2" fontSize={{ base: '16px', md: '30px' }} marginBottom="10px" className="about-title">
            About me
        </Heading>
        <Text id="user-detail-intro" fontSize="16px" lineHeight={1.6}>
            An aspiring Full stack web developer with considerable technical skills who is passionate about coding and designing a website using HTML, CSS, JavaScript, React Js, Redux, MongoDB, Express js, Node js and Chakra UI. And possess self-discipline and the ability to work with minimum supervision. Looking forward to joining a progressive organization with opportunities to work on significant projects.
        </Text>
    </Box>
     </Box>
    );
}

export default About;
